<template>
	<view class="user">
		<!-- 学生信息展示组件 -->
		<view v-for="(itemValue,itemKey) in userInfo" :key="itemKey">
			<view class="user_info_display" v-if="itemKey=='userHeight'||itemKey=='isVip'||itemKey=='className'||itemKey=='userStage'||itemKey=='createTime'||itemKey=='userName'">
				<text>{{itemKey | itemTile}}</text>
				<view class="user_info_value">{{itemValue}}</view>
			</view>
		</view>
		<view>
			<slot></slot>
		</view>
		<view class="text_msg">我放在默认插槽里面</view>
		<button type="primary" plain="true" class="delete" @click="deleteFn">删除</button>
	</view>
</template>

<script>
	export default {
		data(){
			return {

			}
		},
		props:{
			userInfo:{
				type:Object,
				default:()=>{
					return{
						userName:'liWei', //学生姓名
						userHeight:'180', //学生身高
						isVip:false, //是否vip
						className:'ceWeiXinxi',	//班级名称
						userStage:'学霸',	//学生登记
						createTime:'创建时间',	//创建时间
						isEdit:false, //是否编辑
					}
				}
			}
		},
		filters:{
			itemTile(item){
				switch(item){
					case 'userName':
					return '姓名'
					case 'userHeight':
					return '身高'
					case 'isVip':
					return '是否VIP'
					case 'className':
					return '班级名称'
					case 'userStage':
					return '学生等级'
					case 'createTime':
					return '创建时间'
					default :
					return '大佬您好,我是您的忠实小迷弟,这里的filter用得还行吧,请给我面试加分噢'
				}
			}
		},
		methods:{
			deleteFn(){
				let that = this;
				uni.showModal({
					title:'您确定要删除该用户信息吗',
					content: '删除后无法恢复',
					success: function (res) {
						if (res.confirm) {
							
							that.$emit('deleteFn')
							
						} else if (res.cancel) {
							console.log('用户点击取消');
						}
					}
				})
			}
		}
		
	}
</script>

<style lang="scss">
	.user{
		width: 100%;
		overflow: hidden;
		font-size: 28upx;
		color: #666;
		border-bottom: 2upx solid  #e5e5e5;
		padding-bottom: 10upx;
		.user_info_display{
			display: flex;
			justify-content: space-between;
			margin: 20upx 20upx 4upx 20upx;
			.user_info_value{
				margin-left: 100upx;
				flex: 1;
				text-align: right;
				@include dotted3
			}
		}
		.text_msg{
			margin: 6upx 0upx 0upx 20upx;
			
		}
		.delete{
			margin: 0 20upx;
			margin-top: 10upx;
		}
		
	}
</style>
